y{font-size:calc(.95rem * .8);padding:1.5rem}}.intro-msg .intro-card .card-footer{display:flex;flex-flow:column;align-items:flex-end;padding-bottom:2rem}.intro-msg .intro-card .card-footer .resume{font-size:1.7rem;font-family:"Oxygen",sans-serif;font-weight:700;padding:.5rem 3rem .5rem 0}@media all and (max-width: 768px){.intro-msg .intro-card .card-footer .resume{font-size:calc(1.6rem * .8);padding:.5rem 10% .5rem 0}}@media all and (max-width: 768px){.intro-msg .intro-card .card-footer .resume{font-size:calc(1.6rem * .7)}}.intro-msg .intro-card .card-footer .border{width:67%;height:.6rem;background-color:#E5AA50}


